/* Variable definitions */
/* Reusable Styles */
.light-text { color: #aaa; }

[v-cloak] { display: none; }

html { height: 100%; }

form.survey .question-list .survey-question .description { color: #444; font-size: 14px; margin: 3px 0px 10px; }

form.survey .action-list { text-align: right; margin-bottom: 15px; }

form.survey .action-list input[type="submit"][name=submit] { background-color: #5cb85c; border-color: #4cae4c; color: white; }

form.survey .action-list input[type="submit"][name=submit]:hover { background-color: #449d44; }

form.survey .action-list input[type="submit"][name=clear] { background-color: #337ab7; border-color: #2e6da4; color: white; }

form.survey .action-list input[type="submit"][name=clear]:hover { background-color: #286090; }

.survey-response .value .media img, .survey-response .value .media audio, .survey-response .value .media video { max-width: 100%; }

.survey-response .value .media.video { width: 100%; height: 0; padding-bottom: 177.77778%; }

.survey-response .value .media.video video { width: 100%; height: 100%; }

.survey-response .value .media.audio { width: 100%; }

.survey-response .value .media.audio audio { width: 100%; }



