/* Variable definitions */
/* Reusable Styles */
.light-text { color: #aaa; }

[v-cloak] { display: none; }

html { height: 100%; }

.LoginPage .content-card { max-width: 550px; margin: 0px auto; }

.LoginPage .auth-message p { font-size: 18px; }

.LoginPage .auth-message .email { font-weight: 600; }

.LoginPage .oauth { padding-top: 20px; margin: 0px; text-align: center; }

.LoginPage .oauth .facebook-auth { display: inline-block; color: white; background: #3d5a97; padding: 20px 30px; font-size: 20px; border-radius: 5px; font-family: 'Open Sans'; margin-bottom: 0px; }

.LoginPage .or-section { position: relative; font-size: 18px; font-weight: 300; z-index: 1; overflow: hidden; text-align: center; color: #888; margin-top: 20px; }

.LoginPage .or-section:before, .LoginPage .or-section:after { content: '~'; }

.LoginPage .form-tabs { margin-top: 20px; }

.LoginPage .form-tabs ul.nav { text-align: center; }

.LoginPage .form-tabs ul.nav li { float: none; display: inline-block; font-size: 18px; }

.LoginPage .form-tabs ul.nav li a { color: #888; font-weight: 400; }

.LoginPage .form-tabs ul.nav li.active a { color: #333; }

.LoginPage .form-tabs .tab-pane { padding-top: 20px; }

.LoginPage .form-tabs .tab-pane .message { text-align: center; }

.LoginPage .form-tabs .tab-pane form .field { margin-top: 10px; }

.ProfilePage .profile-card .image { width: 66.6%; height: 0px; padding-bottom: 66.6%; margin: auto; border-radius: 50%; background-position: center; background-size: cover; }

.ProfilePage .profile-card .title { text-align: center; margin-bottom: 8px; }

.ProfilePage .action-list { text-align: center; }

.ProfilePage .action { cursor: pointer; user-select: none; border-radius: 2em; color: white; padding: 0.5em 1em; font-size: 16px; font-weight: 600; font-family: "Open Sans", -apple-system, system-ui, sans-serif; border: 1px solid #ccc; display: inline-block; -webkit-font-smoothing: antialiased; margin-bottom: 0.5em; box-shadow: -1.5px -1.5px 0px rgba(0, 0, 0, 0.1) inset; transform: scale(1); transition: background-color 0.3s, transform 0.3s, border 0.3s; }

.ProfilePage .action:hover { transform: scale(1.03); }

.ProfilePage .action:active { transform: scale(0.95); }

.ProfilePage .action.with-icon .icon { transition: transform 0.4s; transform: scale(1); }

.ProfilePage .action.with-icon .icon:hover { transform: scale(1.08); }

.ProfilePage .action:hover { text-decoration: none; }

.ProfilePage .action.full-width { display: block; }

.ProfilePage .action.with-icon { position: relative; padding-left: 2.4em; }

.ProfilePage .action.with-icon .icon { position: absolute; left: 0.3em; top: 0.3em; bottom: 0.3em; width: 1.8em; border-radius: 1em; background-size: contain; background-repeat: no-repeat; }

.ProfilePage .action.primary { background: #836953; border-color: #775f4b; }

.ProfilePage .action.primary:hover { background-color: #735c49; }

.ProfilePage .action.primary .icon { color: #836953; }

.ProfilePage .action.secondary { background: #aaaab2; border-color: #9f9fa8; }

.ProfilePage .action.secondary:hover { background-color: #9d9da6; }

.ProfilePage .action.secondary .icon { color: #aaaab2; }

.ProfilePage .action.green { background: #5cb85c; border-color: #4eb24e; }

.ProfilePage .action.green:hover { background-color: #4cae4c; }

.ProfilePage .action.green .icon { color: #5cb85c; }

.ProfilePage .action.blue { background: #3c87c7; border-color: #357db9; }

.ProfilePage .action.blue:hover { background-color: #347ab5; }

.ProfilePage .action.blue .icon { color: #3c87c7; }

.ProfilePage .action.red { background: #cb4a4a; border-color: #c63a3a; }

.ProfilePage .action.red:hover { background-color: #c33838; }

.ProfilePage .action.red .icon { color: #cb4a4a; }

.ProfilePage .action.orange { background: #e88a46; border-color: #e67f34; }

.ProfilePage .action.orange:hover { background-color: #e57c2f; }

.ProfilePage .action.orange .icon { color: #e88a46; }

.ProfilePage .action.purple { background: #b19cd9; border-color: #a58dd3; }

.ProfilePage .action.purple:hover { background-color: #a28ad2; }

.ProfilePage .action.purple .icon { color: #b19cd9; }

.EditProfilePage .container.form { max-width: 650px; }

.EditProfilePage .back-button { margin-bottom: 0px; }

.EditProfilePage .edit-form { margin-top: 40px; }



