form input.text:-ms-input-placeholder, form input[type=text]:-ms-input-placeholder, form input[type=password]:-ms-input-placeholder, form input[type=number]:-ms-input-placeholder, form input[type=email]:-ms-input-placeholder, form input[type=file]:-ms-input-placeholder, form input.file:-ms-input-placeholder, form textarea:-ms-input-placeholder, form select:-ms-input-placeholder { font-style: italic; color: #bbb; }

